
.hot-container {
  width: 100%;
}
.hot-header .title{
  font-size: 1.6rem;
  height: 4.6rem;
  line-height: 4.6rem;
  color: #666;
  font-weight: 500;
  margin-left: 2rem;
}
.swiper-container1{
  width: 100%;
  overflow-x: hidden;
}
.swiper-container1 .swiper-wrapper{
  width:100%;
}
.swiper-container1 .swiper-wrapper .swiper-slide{
  float: left;
  height: 12rem;
  width: 80% !important;
  /*margin-right: 1rem;*/
  float: left;
}
.swiper-container1 .swiper-slide img{
  width: 100%;
  display: block;
  height: 100%
}



