
.swiper-container3{
  background-color: #fff;
  /*border: 1px solid #000;*/
  overflow: hidden;
  position: relative;
}
.swiper-container3 .swiper-slide img {
  width: 21rem;
  height: 21rem;
  display: block;
  margin: 0 auto;
  margin-bottom: 1rem;
}
.swiper-container3 .swiper-pagination{
  position: absolute;
  bottom: 0;
  left: 0;
}
.swiper-container3 .swiper-pagination-bullet{
  background-color: #ff8282;
}
.good-info {
  padding-top: 2rem;
  width: 100%;
  background-color: #fff;
}
.good-info .good-text{
  text-align: center;
  font-size: 1.8rem;
  font-weight: 500;
}
.good-info .good-price{
  text-align: center;
  font-size: 1.6rem;
  color: #f55669;
}
.selection-fittings{
  width: 100%;
  padding-top: 1rem;
}
.selection-fittings .dl {
  padding-left: 1rem;
  margin-bottom: 1rem;
  margin-right: 2rem;
}
.selection-fittings .dl .dt {
  display: inline-block;
  font-size: 1.6rem;
  margin-right: 2rem;
  color: #a2a0a3;
}
.selection-fittings .dl .dd {
  font-size: 1.6rem;
  color: #3c363e;
  background-color: #fff;
  border: 1px solid #fff;
  border-radius: 5px;
  padding: .5rem;
  margin-right:  2rem;
  margin-bottom: 1rem;
  display: inline-block;
}
.selection-fittings .dl .dd.selected {
  border-color: #f55669;
}
.selection-fittings .btn {
  width: 3rem;
  height: 3rem;
  background-color: #fff;
  display: inline-block;
  text-align: center;
  line-height: 3rem;
  font-size: 1.8rem;
}
.selection-fittings .selectedNum {
  width: 5rem;
  height: 3rem;
  display: inline-block;
  background-color: #fff;
  margin-left: 1rem;
  margin-right: 1rem;
  text-align: center;
  line-height: 3rem;
}
.good-promise {
  width: 100%;
  height: 4.4rem;
  line-height: 4.4rem;
  background-color: #fff;
}
.good-promise .promises{
  width: 100%;
  margin: 0 auto;
}
.good-promise .item {
  width: 24%;
  height: 4.4rem;
  /*margin-left: 1rem;*/
  display: inline-block;
  font-size: 1.2rem;
  color: #c3c3c3;
  text-align: center;
}
.good-promise .icon {
  width: 1.5rem;
  height: 1.5rem;
  display: inline-block;
  background-size: cover;
  background-repeat: no-repeat;
}
.zp {
  background: url(../../assets/images/.png);
}
.lb{
  background: url(../../assets/images/.png);
}
.th{
  background: url(../../assets/images/ .png);
}
.by{
  background: url(../../assets/images/.png);
}
.detail-basebar {
  width: 100%;
  height: 5rem;
  background-color: #fff;
  z-index: 10;
  position: fixed;
  left: 0;
  bottom: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.detail-basebar span {
  width: 33.33333%;
  height: 100%;
  display: block;
  text-align: center;
  line-height: 5rem;
  margin-left: 0;
  font-size: 1.8rem;
  font-weight: 600;
  color: #fff;
}
.detail-basebar .toCart .buycart{
  position: relative;
  display: block;
  width: 1.8rem;
  height: 1.8rem;
  margin-top: 1.6rem;
  margin-left: 2rem;
  background: url(../../assets/images/iconCar.png) center center no-repeat;
  background-size: cover;
}
.detail-basebar .toCart .buynumber {
  position: absolute;
  background: #f74554;
  min-width: 1rem;
  height: 1.4rem;
  line-height: 1.4rem;
  font-size: 12px;
  color: #fff;
  top: .3rem;
  left: 1.4rem;
  text-align: center;
  padding: 0 .3rem;
  border-radius: 1rem;
  box-sizing: border-box;
  font-style: normal;
}
.detail-basebar .cart-btn {
  background: #ff8c4d;
}
.detail-basebar .buy-btn {
  background: #f55669;
}
.detail-info {
  width: 100%;
  margin-top: 1rem;
}
.mint-tab-container {
  width: 100%;
}
.mint-tab-container-item {
  display: block !important;
}
.mint-cell-value {
  width: 100%;
  display: block!important;
}
.mint-cell-value img {
  width: 100%;
  display: block;
}
.mint-navbar{
  width: 100%;
  border-bottom:1px solid #ececec;
  font-size: 1.6rem;
  z-index: 10;
}
.mint-navbar .mint-tab-item.is-selected {
    border-bottom: 3px solid #f55669;
    color: #f55669;
    font-size: 1.6rem;
}
.toast {
    font-size: 1.6rem ;
    width: 80%;
    margin: 0 auto;
}



