
.header{
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.search-page .header .gohome{
  display: block;
  width: 1.8rem;
  height: 1.8rem;
  margin-left: 1rem;
  margin-right: 1rem;
  background: url(../../assets/images/home.png) no-repeat;
  background-size: cover;
}
.search-page .header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.search-page .header .input-box {
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
  width: 20rem;
  height: 3rem;
  border: 1px solid #cdced1;
  padding: .8rem;
  box-sizing: border-box;
}
.search-page .header .search-btn {
  width: 4.4rem;
  height: 4.4rem;
  line-height: 4.4rem;
  text-align: center;
  color: #817e83;
  font-size: 1.4rem;
}
.result-box .tips {
  width: 100%;
  height: 3rem;
  line-height: 3rem;
  padding-left: 2rem;
  box-sizing: border-box;
  font-size: 1.6rem;
  color: #acacac;
}
.tips .digit {
  color: #f55669;
}
.result-list .desc {
  text-align: center;
  font-size: 1.6rem;
  padding-bottom: 2rem;
  box-sizing: border-box;
}
.desc .keywords {
  color: #f55669;
}
.result-list {
  width: 100%;
  background-color: #fff;
}
.notFound {
  width: 10rem;
  height: 10rem;
  display: block;
  margin: 0 auto;
  padding-top: 5rem;
  margin-bottom: 2rem;
}



